              <!-- <p class="details">韩风egkdjjshtinoavedhjshjkthsdhturiejkdhjkhte</p> -->
              <div class="price fl">&yen;{{ item.price }}</div>
              <van-stepper class="control" v-model="value" integer />
            </div>
          </div>
          <!-- <van-checkbox name="b">复选框 b</van-checkbox>
            <van-checkbox name="c">复选框 c</van-checkbox> -->
        </van-checkbox-group>
      </div>
    </div>
    <van-submit-bar :price="3050" button-text="提交订单">
      <van-checkbox class="chebox" v-model="checked"
        >全选</van-checkbox
      >
    </van-submit-bar>
  </div>
</template>

