import Vue from 'vue'
import App from './App.vue'
import router from './router'

// 从vant组件库导入button
import { Row, Col,RadioGroup,Checkbox, CheckboxGroup,Radio,Card,Button,Tag,Stepper,SubmitBar } from 'vant'

// 根据屏幕适配rem
import 'lib-flexible/flexible'

// 全局注册组件
Vue.use(Row).use(Col).use(RadioGroup).use(Checkbox).use(CheckboxGroup).use(Radio).use(Card).use(Button).use(Tag).use(Stepper).use(SubmitBar)

Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
